import React from "react"
import { Link } from "gatsby"
import classnames from 'classnames'

import { rhythm, scale } from "../utils/typography"
import styles from './layout.module.css'
import ThemeSetter from './themeSetter'

// console.log(process.env)
const rootPath = `${__PATH_PREFIX__}/`

const Header = ({pathname, title}) => {
  return (
    <h1 style={{...scale(1.4), marginBottom: rhythm(1.5)}}>
      <Link style={{
          boxShadow: `none`,
          textDecoration: `none`,
          color: `inherit`,
        }}
        to={`/`}
      >
        {title}
      </Link>
    </h1>
  )
}

class Layout extends React.Component {
  constructor() {
    super();
    this.state = {
      darkMode: false,
    }
  }

  toggleMode = () => this.setState({ darkMode: !this.state.darkMode });

  render() {
    const { title, children, withFooter = true } = this.props;
    const classname = this.state.darkMode ? 'dark_mode' : 'light_mode'

    return (
      <div className={classnames(styles.root_panel, styles[classname])}>
        <div className={styles.menu} style={{ display: 'flex' }}>
          <div style={{ display: 'flex', flexGrow: 1 }}>
            <div className={styles.menu_item}><Link to={"/"}>Home</Link></div>
            <div className={styles.menu_item}><Link to={"/calculators"}>Calculators</Link></div>
          </div>
          <div className={styles.theme_setter}>
            <ThemeSetter darkMode={this.state.darkMode} toggle={this.toggleMode} />
          </div>
        </div>
        <div
          style={{
            marginLeft: `auto`,
            marginRight: `auto`,
            maxWidth: rhythm(24),
            padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
            paddingTop: title ? 5 : 30,
          }}
        >
          {title && (<header><Header title={title} /></header>)}
          <main>{children}</main>
          {withFooter && (
            <footer>
              Â© {new Date().getFullYear()}, Martin Schnurer
            </footer>
          )}
        </div>
      </div>
    )
  }
}

export default Layout
